.app {
    padding: 3rem 1.5rem;
}
.sets-list {
    margin: 0;
    padding: 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    list-style-type: none;
}
.set {
  display: block;
  margin: 1em;
  padding: 1em;
  -webkit-flex-basis: calc(33.33% - 2em);
          flex-basis: calc(33.33% - 2em);
  min-width: 250px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  cursor: pointer;
  max-width: 405px;
  background-color: #fff;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
  color: #4a4a4a;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.set:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.set__logo {
  height: 140px;
  padding-top: 15px;
  text-align: center;
}

.set__logo-img {
  max-height: 100%;
  max-width: 100%;
}

.set__content {
  padding: 1.5rem;
}

.set__media {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.set__media-left {
  margin-right: 1rem;
  -webkit-flex-basis: auto;
          flex-basis: auto;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.set__media-logo {
  height: 48px;
  width: 48px;
}

.set__media-logo-img {
  max-height: 100%;
  max-width: 100%;
}

.set__media-right {
  -webkit-flex-basis: auto;
          flex-basis: auto;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  text-align: left;
}

.set__media-title {
  font-size: 1.5rem;
  font-weight: 300;
  word-break: break-word;
}

.set__media-subtitle {
  word-break: break-all;
}

.set__list {
  list-style: disc;
  margin-top: 1em;
  margin-left: 2em;
}

.cards {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.cards-home {
    font-family: "Segoe UI","Fira Sans","Droid Sans","Helvetica Neue",Helvetica,Arial,sans-serif;
    color: brown;
}

.cards-home:hover {
    color: aquamarine;
}

.cards-list {
    margin: 0;
    padding: 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    list-style-type: none;
}
.card__pic {
    margin: 10px 20px 10px 0;
    width: 240px;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.card__pic:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }

.card__pic-img {
    height: 100%;
    width: 100%;
    background: url(https://pokemontcg.io/static/media/card-back-small.ce3a314e.png) transparent no-repeat 50%;
    background-size: 95%;
}
* {
    box-sizing: border-box;
}

html {
    font-size: 16px;
    background-color: #fff;
}

body {
    font-family: "Segoe UI","Fira Sans","Droid Sans","Helvetica Neue",Helvetica,Arial,sans-serif;
    line-height: 1.5;
    color: #4a4a4a;
    font-weight: normal;
}

ul {
    padding: 0;
    margin: 0;
  }

p {
    margin: 0;
    padding: 0;
}

figure {
    margin: 0;
    padding: 0;
}

a {
    color: #4a4a4a;
    text-decoration: none;
}
